import React, { useContext, useState, useEffect } from 'react';
import { useRouter } from 'next/router';
import { ShowContext } from '../src/context/ShowContext';
import Layout from '../src/components/Layout';

const Show = () => {
	const [router] = useState(useRouter())
	const [show, setShow] = useState({});

	useEffect(() => {
		const getShow = async () => {
			const showId = await router.query.show;
			const res = await fetch(`https://api.tvmaze.com/shows/${router.query}`);
			const data = await res.json();
			setShow(data);
			console.log(data);
			return data;
		};
		getShow();
	}, [router.query.show]);
	const isImage = show.image
		? show.image.medium
		: 'https://static.tvmaze.com/images/no-img/no-img-portrait-text.png';
	return (
		<Layout>
			<h1>{show.name}</h1>
			<img src={isImage} alt={show.name} />
		</Layout>
	);
};

export default Show;
